<template>
    <div id="goodsSearch">
		<ul>
			<li v-for="(item,index) in list">
				<img :src="source(index)">

				<div class="nav">
					<p class="title">{{index+1}}.{{item.name}}</p>
					<p class="content">{{item.content}}</p>
					<p class="value">￥{{item.value}}</p>
				</div>
				</li>
		</ul>
	</div>
</template>
<script>
export default{
	data(){
		return{
			food:"",
			searchList:[]
		}
	},
	methods:{
		show(ev){
			if(ev.keyCode == 13){
				this.search()
			}
		},

		source(index){
            // console.log(index)
			return (require("../pic/"+this.$store.state.id+"/"+index+".jpg"))
		}
	},
	computed:{
		list(){
		    return this.searchList.length==0 ? this.$store.state.goods[this.$store.state.id] : this.searchList
		},

	},
	mounted(){
		// console.log(this.$store.state.id)
	}
}
</script>
<style lang='less'>
#goodsSearch{
	ul{
		list-style-type: none;
		padding: 0;
		margin: 0;
		li{
			width: 100%;
			height: 80px;
			margin: 20px 0 ;
			overflow: hidden;
			background: #f3f5f7;
			img{
				width: 80px;
				height: 80px;
				float: left;
			}
			.nav{
				height: 100px;
				width: 280px;
				float: left;
				.title{
					padding-left: 12px;
					color: #000;
				}
				.content{
					padding:10px 12px;
					font-size: 12px;
					color: #999;
				}
				.value{
					color: red;
					padding-left: 12px;
					font-size: 16px;
				}
			}
			}
		}
	}

</style>


