<template>
    <div id="head" >
        <p @click="back"><span>{{this.in}}</span></p>
    </div>
</template>
<script>
export default {
	data(){
		return{

		}   
	},
	methods:{
		back(){
			this.$router.push("/menu")
		}
	},
	computed:{
	    in(){
	        return this.$store.state.name==""? "首页菜单":this.$store.state.name+"-点击返回"
	}
	}
}
</script>
<style lang='less'>
#head{

    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #1c1b20;
    height: 40px;
    text-align: center;
    line-height: 40px;
    color: #fff;
    font-size: 20px;
    p{
        font-size: 18px;
        cursor:pointer;
        span{
            margin-left: 10px ;
        }
    }
}
</style>



